
.nprogress-container {
  position: fixed !important;
  width: 100%;
  height: 50px;
  z-index: 2048;
  pointer-events: none;
}
.nprogress-container #nprogress .bar {
  background: #004c32;
}
.nprogress-container #nprogress .peg {
  box-shadow: 0 0 10px #004c32, 0 0 5px #004c32;
}
.nprogress-container #nprogress .spinner-icon {
  border-top-color: #004c32;
  border-left-color: #004c32;
}
.container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  width: 100%;
  max-width: 100%;
}
.container .auto-width {
  width: 230px;
}
.container .menu-box {
  background: #eef1f6;
}
.container .fa-user-o {
  margin: 0px 5px;
}
.container .header {
  height: 60px;
  line-height: 60px;
  background: #20a0ff;
  color: #fff;
}
.container .header .userinfo {
  text-align: right;
  float: right;
}
.container .header .userinfo .select-language {
  width: 120px;
}
.container .header .userinfo .user-info {
  display: inline-block;
}
.container .header .userinfo .user-info span {
  display: inline-block;
}
.container .header .userinfo .user-info span.nickname {
  margin-right: 15px;
  font-size: 18px;
}
.container .header .userinfo .user-info span.logout {
  width: 60px;
  height: 59px;
  line-height: 59px;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  border-radius: 4px;
  background: #20a0ff;
  cursor: pointer;
}
.container .header .userinfo .userinfo-inner {
  cursor: pointer;
  color: #fff;
}
.container .header .userinfo .userinfo-inner img {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  margin: 10px 0px 10px 10px;
  float: right;
}
.container .header .userinfo .el-select {
  vertical-align: top;
  margin-left: 20px;
}
.container .header .logo {
  height: 60px;
  font-size: 22px;
  padding-left: 20px;
  padding-right: 20px;
  border-color: rgba(238, 241, 146, 0.3);
  border-right-width: 1px;
  border-right-style: solid;
}
.container .header .logo img {
  width: 40px;
  float: left;
  margin: 10px 10px 10px 18px;
}
.container .header .logo .txt {
  color: #fff;
}
.container .header .logo-width {
  width: 230px;
}
.container .header .logo-collapse-width {
  width: 65px;
}
.container .header .tools {
  padding: 0px 23px;
  width: 14px;
  height: 60px;
  line-height: 60px;
  cursor: pointer;
}
.container .header .tools i.fa-align-justify {
  line-height: 60px;
}
.container .main {
  display: flex;
  position: absolute;
  top: 60px;
  bottom: 0px;
  overflow: hidden;
}
.container .main aside {
  flex: 0 0 230px;
  width: 230px;
}
.container .main aside .el-menu {
  height: 100%;
  overflow-y: auto;
}
.container .main aside .collapsed {
  width: 60px;
}
.container .main aside .collapsed .item {
  position: relative;
}
.container .main aside .collapsed .submenu {
  position: absolute;
  top: 0px;
  left: 60px;
  z-index: 99999;
  height: auto;
  display: none;
}
.container .main .menu-collapsed {
  flex: 0 0 60px;
  width: 60px;
}
.container .main .menu-collapsed .el-menu {
  width: 100% !important;
}
.container .main .menu-expanded {
  flex: 0 0 230px;
  width: 230px;
}
.container .main .menu-expanded .el-menu {
  width: 100% !important;
}
.container .main .content-container {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
}
.container .main .content-container .breadcrumb-container .title {
  width: 200px;
  float: left;
  font-size: 1rem;
  font-weight: normal;
}
.container .main .content-container .breadcrumb-container .breadcrumb-inner {
  float: right;
}
.container .main .content-container .content-wrapper {
  background-color: #fff;
  box-sizing: border-box;
}



